.card {
  position: absolute;
  width: 120px;
  height: 170px;
  background-color: #f2f2f2;
  border-radius: 9px;
  flex-shrink: 0;
  padding: 3px;
  box-shadow: 0px 2.14286px 14.5714px rgba(0, 0, 0, 0.1);
  filter: drop-shadow(0px 12.5406px 29.8587px rgba(0, 0, 0, 0.15));
  transition: transform 0.2s, border 0.2s;
  backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  border: 5px transparent solid;
}

.cardValueSuitContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.active {
  border: 5px #0b9dff solid;
}

.win {
  border: 5px #7ad25b solid;
}

.loss {
  border: 5px #f32828 solid;
}

.draw {
  border: 5px #ff8440 solid;
}

.suitContainerRight {
  align-items: flex-end;
}

.cardValue {
  position: relative;
  right: 3px;
  display: inline-block;
  width: 22px;
  text-align: center;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 21px;
  line-height: 25px;
  user-select: none;
}

.cardValueRight {
  left: 3px;
  right: 0;
}

.suitSmall {
  display: block;
  width: 16px;
  height: 16px;
  transform: scale(2);
}

.suitBig {
  text-align: center;
  margin-top: 10px;
  & > svg {
    width: 58px;
    height: 58px;
    transform: scale(2);
  }
}

.diamond {
  color: #bf0000;
  fill: #bf0000;
}

.trefle {
  color: #0e007d;
  fill: #0e007d;
}

.heart {
  color: #bf0000;
  fill: #bf0000;
}

.pique {
  color: #0e007d;
  fill: #0e007d;
}
