.root {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transform-origin: top center;
  gap: 5px;

  height: 600px;

  @media (max-width: 600px) {
    padding: 5px;
    gap: 5px;
    height: 240px;
    transform: scale(0.5);
  }

  @media (max-width: 600px) {
    transform: scale(0.58);
  }
}

.userBox {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}

$defaultRootSize: 600px;
$mobileScreen: 600px;

:export {
  #{"defaultRootSize"}: $defaultRootSize;
  #{"mobileScreen"}: $mobileScreen;
}

.wrapper {
  height: calc(100vh - 100px - 50px - 150px);
  max-height: $defaultRootSize;

  @media (max-width: $mobileScreen) {
    height: fit-content;
    max-height: 100%;
  }
}
