.root {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: nowrap;
  @media (max-width: 500px) {
    transform: translateY(-20px);
  }
}

.hand {
  transition: all 0.5s;
  display: flex;
}

.score {
  background: #3a3a3a;
  border-radius: 14px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  padding: 0 20px;
  color: #ffffff;
  margin-top: 5px;
  @media (max-width: 500px) {
    transform: translateY(5px);
  }
}

.active {
  background: #0b9dff;
  color: #150a30;
}

.win {
  background: #7ad25b;
  color: #150a30;
}

.lose {
  background: #f32828;
  color: #150a30;
}

.draw {
  background: #ff8440;
  color: #150a30;
}
