.labelsWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  font-weight: 500px;
  font-size: 14px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.7px;
}

.ribbonWrapper {
  position: relative;
  display: flex;
  background: #566074;
  height: 30px;
  width: 208px;
  align-items: center;
  justify-content: center;
  transform-style: preserve-3d;

  &::before {
    position: absolute;
    content: "";
    border: 15px solid #4f5768;
    width: 39px;
    top: 4px;
    left: -25px;
    border-left: 15px solid transparent;
    transform: translateZ(-1px);
  }
  &:after {
    position: absolute;
    content: "";
    right: -25px;
    top: 4px;
    border: 15px solid #4f5768;
    width: 39px;
    border-right: 15px solid transparent;
    transform: translateZ(-1px);
  }
}
