$defaultRootSize: 600px;
$mobileScreen: 600px;

:export {
  #{"defaultRootSize"}: $defaultRootSize;
  #{"mobileScreen"}: $mobileScreen;
}

.wrapper {
  height: calc(100vh - 100px - 50px - 150px);
  max-height: $defaultRootSize;

  @media (max-width: $mobileScreen) {
    height: fit-content;
    max-height: 100%;
  }
}

.root {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  transform-origin: top center;

  @media (max-width: $mobileScreen) {
    height: 240px;
    transform: scale(0.6);
  }
}

.cardsTop {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: $mobileScreen) {
    & > img {
      width: 300px;
    }
  }
}

.cardsBottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: $mobileScreen) {
    & > img {
      width: 300px;
    }
  }
}

.number {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 59px;
  height: 20px;
  border-radius: 10px;
  background: #3a3a3a;
  font-weight: bold;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
}

.green {
  background: #7ad25b;
  color: #150a30;
}
