.root {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: 500px) {
    transform: translateY(-10px);
  }
}

.hand {
  transition: all 0.5s;
  display: flex;
}

.score {
  background: #3a3a3a;
  border-radius: 14px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  padding: 0 20px;
  color: #ffffff;
  margin-bottom: 5px;
  @media (max-width: 500px) {
    margin-bottom: 0;
    transform: translateY(10px);
  }
}
