.cardWrapper {
  position: relative;
  width: 80px;
  height: 170px;
  flex-shrink: 0;
  transition: transform 0.5s;
  transform-style: preserve-3d;
  visibility: visible;
  width: 80px;
  transition: all 0.5s;
  @media (max-width: 500px) {
    width: 20px;
    transform: scale(0.8);
  }
}

.open {
  transform: rotateY(0deg);
  animation-name: openCardDesktop;
  animation-duration: 0.3s;
  @media (max-width: 500px) {
    transform: rotateY(0deg) scale(0.8);
    animation-name: openCardMobile;
  }
}

@keyframes openCardDesktop {
  from {
    transform: rotateY(180deg);
  }
  to {
    transform: rotateY(0deg);
  }
}

@keyframes openCardMobile {
  from {
    transform: rotateY(180deg) scale(0.8);
  }
  to {
    transform: rotateY(0deg) scale(0.8);
  }
}

.hide {
  transform: rotateY(180deg) translate(-40px, 0px);
  @media (max-width: 500px) {
    transform: rotateY(180deg) translate(-80px, 0px) scale(0.8);
  }
}

.move {
  width: 0;
  visibility: hidden;
}

.translate {
  width: 80px;
  visibility: visible;
  transition: all 0.5s;
}

.translateHidden {
  width: 80px;
  visibility: visible;
  transition: all 0.5s;
  transform: rotateY(180deg) translate(-40px, 0px);
  @media (max-width: 500px) {
    width: 20px;
    transform: rotateY(180deg) translate(-80px, 0px) scale(0.8);
  }
}

.rotate {
  transform: rotateY(180deg) translate(-40px, 0px);
  @media (max-width: 500px) {
    width: 20px;
    transform: rotateY(180deg) translate(-80px, 0px) scale(0.8);
  }
}
